@import 'ext/solarized-light';

.highlight {
	background-color: #f4f4f4;
	font-size: 0.9em;
}

div.highlight {
	padding: 5px 20px;
}
pre.highlight, code {
	font-family: 'Consolas', Menlo, monospace;
	line-height: 1.4em;
	tab-size: 4;
}

code.highlighter-rouge {
	background-color: #f4f4f4;
	color: #586e75;
}
